  margin-block-start: 0; }

h2 {
  font-size: 1.5625rem;
  font-weight: 700;
  margin-block-start: 0; }

ul {
  margin: 0;
  padding: 0; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1140px;
  padding: 0 20px;
  box-sizing: border-box;
  margin: 0 auto; }

.header__menu {
  padding: 50px 0; }

.header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.header__logo-img {
  width: 85px;
  height: 85px; }

.header__logo-text {
  display: none;
  font-size: 0.8125rem;
  font-weight: 500; }

.header__contacts {
  max-height: 20px;
  height: 100%;
  display: flex;
  align-items: center; }

.header__contacts-img {
  margin: 0;
  padding: 0;
  height: 15px; }

.header__contacts-phone {
  margin-left: 20px;
  font-size: 0.8125rem;
  font-weight: 500; }

@media (min-width: 768px) {
  .header__logo {
    display: flex;
    align-items: center; }
  .header__logo-text {
    display: inline-block;
    margin-left: 20px; } }

.main__wrap {
  height: 890px; }

.main__container {
  position: relative; }

.content {
  padding-top: 15px;
  display: flex;
  flex-flow: column; }

.description__title {
  display: none; }

.description__title-mobile {
  margin-bottom: 30px; }

.description__text {
  font-size: 0.9375rem;
  font-weight: 500;
  margin-bottom: 20px;
  line-height: 1.3125rem; }

.description__list {
  margin-left: 20px;
  margin-bottom: 50px; }

.description__list-elem {
  font-size: 0.9375rem;
  font-weight: 500;
  margin-bottom: 15px;
  line-height: 1.3125rem; }
  .description__list-elem:last-child {
    margin-bottom: 0; }

.callback {
  margin: 0 auto; }

.callback__block {
  padding: 65px 15px;
  background-color: #fff;
  box-shadow: 5px 5px 5px rgba(255, 255, 255, 0.2);
  border-radius: 30px;
  max-width: 376px;
  width: 100%; }

.callback__title {
  margin: 0 auto 40px;
  text-align: center;
  color: #050d4e;
  font-size: 0.9375rem;
  font-weight: 600; }

.callback__form {
  text-align: center; }

.callback__form-input {
  max-width: 306px;
  width: 100%;
  padding: 15px;
  font-size: 0.8125rem;
  font-weight: 600;
  color: #050d4e;
  background: rgba(5, 13, 78, 0.1);
  border-radius: 10px;
  border: none;
  margin-bottom: 20px;
  outline: none; }
  .callback__form-input::placeholder {
    color: rgba(5, 13, 78, 0.3); }

#formTel {
  margin-bottom: 40px; }

.callback__form-button {
  max-width: 232px;
  width: 100%;
  margin: 0 auto; }

.callback__form-submit {
  padding: 19px 50px;
  border: none;
  border-radius: 10px;
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  text-align: center;
  overflow: hidden;
  outline: none; }
  .callback__form-submit::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 240px;
    height: 60px;
    transition: all .5s ease-in-out;
    background: linear-gradient(92.64deg, rgba(193, 68, 155, 0.9) 4.31%, rgba(133, 56, 174, 0.9) 51.62%, rgba(81, 53, 204, 0.9) 100%); }

.callback__form-button:active .callback__form-submit::before {
  right: 0;
  bottom: 0;
  content: '';
  position: absolute;
  width: 240px;
  height: 60px;
  background: linear-gradient(92.64deg, rgba(193, 68, 155, 0.9) 4.31%, rgba(133, 56, 174, 0.9) 51.62%, rgba(81, 53, 204, 0.9) 100%);
  transform: scale(10) translate(-44%, -44%); }

.callback__form-button:hover .callback__form-submit::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 240px;
  height: 60px;
  background: linear-gradient(92.64deg, rgba(193, 68, 155, 0.9) 4.31%, rgba(133, 56, 174, 0.9) 51.62%, rgba(81, 53, 204, 0.9) 100%);
  transform: scale(8) rotate(90deg) translate(110px, 0); }

.callback__form-button:focus .callback__form-submit::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 240px;
  height: 60px;
  background: linear-gradient(92.64deg, rgba(193, 68, 155, 0.9) 4.31%, rgba(133, 56, 174, 0.9) 51.62%, rgba(81, 53, 204, 0.9) 100%);
  transform: scale(10) translate(44%, 44%); }

.callback__button-text {
  color: #fff;
  font-size: 0.9375rem;
  font-weight: 600;
  z-index: 2; }

@media (min-width: 375px) {
  .main__wrap {
    height: 830px; } }

@media (min-width: 425px) {
  .main__wrap {
    height: 740px; } }

@media (min-width: 480px) {
  .main__wrap {
    height: 680px; } }

@media (min-width: 768px) {
  .main__wrap {
    height: 436px; }
  .main__container {
    position: relative; }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    padding-top: 55px;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-flow: row; }
  .callback__block {
    width: 376px;
    margin-left: 69px; } }

@media (min-width: 1000px) {
  .content {
    justify-content: space-between; }
  .callback__block {
    margin-left: 160px; }
  .description__title-mobile {
    display: none; }
  .description__title {
    display: block;
    margin-bottom: 50px; }
  .description__text {
    font-size: 1.25rem;
    margin-bottom: 30px;
    line-height: 1.75rem; }
  .description__list-elem {
    font-size: 1.25rem;
    line-height: 1.75rem; } }

.wrapper {
  width: 100%;
  height: 380px; }

.footer__img img {
  object-fit: cover;
  width: 100%;
  height: 100%; }

@media (min-width: 768px) {
  .wrapper {
    height: 288px; } }
